import * as API from '@/api/applicationMarket'
import {
  asyncFunc
} from '@/utils'

import {
  jumpPreview
} from '@/utils/url'
import {
  Loading
} from 'element-ui';

const mixins = {
  data() {
    return {
      LoadingFactory: Loading
    }
  },
  methods: {
    // 
    async appUpgrade(appId) {
      // 
      let loadingInstance = this.LoadingFactory.service()
      const [err, res] = await asyncFunc(API.publishNote, {
        appId
      })
      loadingInstance.close();
      if (err) {
        return Promise.reject()
      }

      const {
        data: {
          appName,
          note,
          version
        },
      } = res

      // 
      const h = this.$createElement
      await this.$msgbox({
        title: '',
        message: h('div', {
          class: 'upgrade-desc-box'
        }, [
          h('div', {
            class: 'upgrade-title'
          }, `${appName} ${version}`),
          h('div', {
            class: 'upgrade-desc'
          }, note),
        ]),
        showCancelButton: true,
        confirmButtonText: '',
        cancelButtonText: '',
      })
      // 
      loadingInstance = this.LoadingFactory.service()
      const [, response] = await asyncFunc(API.appUpgrade, {
        appId
      })
      loadingInstance.close();

      if (response) {
        this.$message.success('')
      }
    },
    useApp(item) {
      const url = jumpPreview({
        appVersionId: item.appId,
        appId: item.appId,
        type: 1
      })
      window.open(url)
    },
    // 
    async appInstall(item) {
      // 
      let loadingInstance = this.LoadingFactory.service()
      const [publicErr, publicRes] = await asyncFunc(API.publishNote, {
        appId: item.appId
      })
      loadingInstance.close();
      if (publicErr) {
        return Promise.reject()
      }

      const {
        data: {
          appName,
          note
        },
      } = publicRes

      // 
      const h = this.$createElement
      await this.$msgbox({
        title: '',
        message: h('div', {
          class: 'upgrade-desc-box'
        }, [
          h('div', {
            style: {
              fontWeight: 'bold'
            }
          }, `${appName}`),
          h('div', {
            class: 'upgrade-desc'
          }, note),
        ]),
        showCancelButton: true,
        confirmButtonText: '',
        cancelButtonText: '',
      })
      // 
      loadingInstance = this.LoadingFactory.service()
      const [err, res] = await asyncFunc(API.appInstall, {
        appId: item.appId
      })
      loadingInstance.close();
      if (res) {
        this.$message.success('')
      }

    },
    // 
    async appUninstall(item) {
      const isShortcut = item.type === 'SHORTCUT'
      const operateTitle = isShortcut ? '' : ''
      await this.$confirm(`${operateTitle}${isShortcut ? '' : ''}${item.appName}`, `${operateTitle}`)
      // 
      let loadingInstance = this.LoadingFactory.service()
      const method = isShortcut ? API.appShortcutRemove : API.appUninstall
      const params = isShortcut ? {
        id: item.shortcutId
      } : {
        appId: item.appId
      }
      const [err, res] = await asyncFunc(method, params)
      loadingInstance.close();
      if (res) {
        this.$message.success(`${operateTitle}`)
        return Promise.resolve()
      }
      return Promise.reject()
    },
  }
}

export default mixins